import {
  CliRenderer,
  createCliRenderer,
  TextRenderable,
  RGBA,
  GroupRenderable,
  renderFontToFrameBuffer,
  FrameBufferRenderable,
} from "@opentui/core";

let buffer: FrameBufferRenderable | null = null;
let renderer: CliRenderer | null = null;
let parentContainer: GroupRenderable | null = null;
let currentScreenElements: TextRenderable[] = [];
let scrollY = 0;
let contentHeight = 56;

const mockProjects = [
  { id: "1", name: "my-api", envCount: 12 },
  { id: "2", name: "frontend-app", envCount: 8 },
  { id: "3", name: "database-service", envCount: 15 },
];

const mockEnvVars = [
  { key: "DATABASE_URL", value: "postgresql://...", environment: "production" },
  { key: "API_KEY", value: "sk_...", environment: "production" },
  { key: "DEBUG", value: "false", environment: "production" },
];

function clearScreen() {
  if (parentContainer) {
    currentScreenElements.forEach((element) => {
      parentContainer!.remove(element.id);
    });
    currentScreenElements = [];
  }
}

function addElement(element: TextRenderable) {
  if (parentContainer) {
    parentContainer.add(element);
    currentScreenElements.push(element);
  }
}

function showMainMenu() {
  clearScreen();

  const title = new TextRenderable("title", {
    content: "Better Env CLI - Main Menu",
    positionType: "absolute",
    position: { left: 2, top: 12 },
    fg: RGBA.fromInts(255, 255, 255, 255),
    bg: RGBA.fromInts(0, 0, 40, 255),
    zIndex: 20,
  });
  addElement(title);

  const options = [
    "1. View Projects",
    "2. View Environment Variables",
    "3. Create New Project",
    "4. Pull Environment Variables",
    "5. Push Environment Variables",
    "q. Quit",
  ];

  options.forEach((option, index) => {
    const optionText = new TextRenderable(`option-${index}`, {
      content: option,
      positionType: "absolute",
      position: { left: 2, top: 16 + index },
      fg: RGBA.fromInts(200, 200, 200, 255),
      bg: RGBA.fromInts(0, 0, 40, 255),
      zIndex: 20,
    });
    addElement(optionText);
  });
}

