export async function run(): Promise<void> {
  renderer = await createCliRenderer();
  renderer.start();
  renderer.setBackgroundColor("#000028");

  buffer = new FrameBufferRenderable("ascii-art", {
    width: renderer.terminalWidth,
    height: 20,
    positionType: "absolute",
    position: {
      left: 0,
      top: 2,
    },
    zIndex: 20,
  });

  parentContainer = new GroupRenderable("main-container", {
    positionType: "absolute",
    position: {
      left: 0,
      top: 0,
    },
    zIndex: 15,
    visible: true,
  });
  renderer.root.add(parentContainer);
  renderer.root.add(buffer);

  renderFontToFrameBuffer(buffer.frameBuffer, {
    text: "BETTER ENV",
    x: 5,
    y: 1,
    fg: RGBA.fromInts(255, 100, 100, 255),
    bg: RGBA.fromInts(0, 0, 40, 255),
    font: "block",
  });

  const subtitleText = new TextRenderable("subtitle", {
    content: "Better Env CLI - Press any key to continue",
    positionType: "absolute",
    position: {
      left: 2,
      top: 12,
    },
    fg: RGBA.fromInts(100, 100, 255, 255),
    bg: RGBA.fromInts(0, 0, 40, 255),
    zIndex: 20,
  });
  renderer.root.add(subtitleText);

  showMainMenu();

  if (!process.stdin.listenerCount("data")) {
    process.stdin.setRawMode(true);
    process.stdin.resume();
    process.stdin.setEncoding("utf8");
  }

  process.stdin.on("data", (key) => {
    const keyStr = key.toString();
    if (keyStr === "q" || keyStr === "\u0003") {
      process.exit(0);
    }
    if (keyStr === "1") {
      showProjects();
    }
    if (keyStr === "2") {
      showEnvVars();
    }
    if (keyStr === "3") {
      showCreateProject();
    }
    if (keyStr === "4") {
      showPullEnv();
    }
    if (keyStr === "5") {
      showPushEnv();
    }
    if (
      keyStr !== "1" &&
      keyStr !== "2" &&
      keyStr !== "3" &&
      keyStr !== "4" &&
      keyStr !== "5"
    ) {
      showMainMenu();
    }
  });
}

run();
